<template>
  <div class="my-collect">
    <ul v-if="this.collectList.length">
      <li v-for="(item,index) in collectList" :key="index">
        <product :leftWidth="600" :productInfo="item">
          <div class="cancelCollect" @click="onCancelCollect(item)">取消收藏</div>
        </product>
      </li>
    </ul>
    <alert-box v-if="alertShow"
               :width='420'
               :height='144'
               :btnTextLeft="'暂不取消'"
               :btnTextRight="'确认取消'"
               @hanleClose="closeAlert"
               @handleBtnLeftEvent="closeAlert"
               @handleBtnRightEvent="handleCancelCollect"
               :absTop='232'
               :absLeft="148"
               :title="'确认取消此收藏？'"></alert-box>
    <no-result v-if="!this.collectList.length"
               :title="'暂无产品收藏'" :src="src"
               :btnText="'查看更多产品'"
               @handleClik="seeMore"
    ></no-result>
    <div class="pagination-wrapper">
    <pagination-bar
      :total="total"
     :limit="limit"
     @handleChange="pageClick"
    ></pagination-bar>
    </div>
  </div>
</template>

<script>
import product from '@/components/BaseProduct'
import alertBox from '@/components/BaseAlertBox'
import noResult from '@/components/BaseNoResult'
import paginationBar from '@/components/ThePaginationBar'

export default {
  name: 'myCollect',
  components: {
    product,
    alertBox,
    noResult,
    paginationBar
  },
  data () {
    return {
      alertShow: false,
      collectList: [],
      page: 1,
      limit: 5,
      total: 0,
      cancelId: '',
      src: require('@/assets/images/no_collect.png')
    }
  },
  mounted () {
    this.getCollectList()
  },
  methods: {
    pageClick (page) {
      this.page = page
      this.getCollectList()
    },
    onCancelCollect (item) {
      this.alertShow = true
      this.cancelId = item.id
    },
    handleCancelCollect () {
      this.alertShow = false
      // 1取消收藏  0 收藏
      let postData = {
        productId: this.cancelId
      }
      this.$api.personCenter.collectOrNot(postData).then(res => {
        if (res.code === 1) {
          this.getCollectList()
        }
      })
    },
    closeAlert () {
      this.alertShow = false
    },
    getCollectList () {
      let postData = {
        page: this.page,
        limit: this.limit
      }
      this.$api.personCenter.getCollectList(postData).then(res => {
        if (res.code === 1) {
          this.collectList = res.data.productList
          this.total = res.data.total
        }
      })
    },
    seeMore () {
      this.$router.push({
        path: '/37duc/product/list'
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .my-collect {
    background: #f7f7f7;

    ul {
      margin-bottom: 12px;
    }
    .pagination-wrapper{
      float: right;
    }
  }
</style>
